import { Component, OnInit } from '@angular/core';

import { FuseTranslationLoaderService } from '@fuse/services/translation-loader.service';
import { FuseConfigService } from '@fuse/services/config.service';
import { FormBuilder, FormGroup, FormControl, Validators, AbstractControl } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';

import { locale as spanish } from './i18n/es';
import { AuthService } from '../auth.service';
import { MatSnackBar, MatSnackBarHorizontalPosition, MatSnackBarVerticalPosition, MatSnackBarConfig, MatSnackBarContainer } from '@angular/material/snack-bar';
import { TranslateService } from '@ngx-translate/core';
import { FuseNavigationService } from '@fuse/components/navigation/navigation.service';
import { BadgesService } from 'app/services/badges.service';

export class PasswordValidation {
    static MatchPassword(form: AbstractControl) {

        let password = form.get('password').value;
        let confirm_password = form.get('confirm_password').value;
        if (password !== confirm_password) {
            form.get('confirm_password').setErrors({ MatchPassword: true });
        } else {
            if (confirm_password === '' || confirm_password === undefined || confirm_password === null) {
                form.get('confirm_password').setErrors({ required: true });
            } else {
                form.get('confirm_password').setErrors(null);
            }
        }
    }
}

@Component({
    selector   : 'collaborator-register',
    templateUrl: './collaborator-register.component.html',
    styleUrls  : ['./collaborator-register.component.scss']
})
export class CollaboratorRegisterComponent implements OnInit {
    
    form: FormGroup;

    public horizontalPosition: MatSnackBarHorizontalPosition = 'center';
    public verticalPosition: MatSnackBarVerticalPosition = 'top';

    constructor(
        private router: Router,
        private authService: AuthService,
        private badgesService: BadgesService,
        private _fuseTranslationLoaderService: FuseTranslationLoaderService,
        private _fuseNavigationService: FuseNavigationService,
        private translateService: TranslateService,
        private _fuseConfigService: FuseConfigService,
        private snackbar: MatSnackBar,
        private formBuilder: FormBuilder,
        private activatedRoute: ActivatedRoute
    ) {
        this._fuseTranslationLoaderService.loadTranslations(spanish);
    }

    ngOnInit() {
        this.form = this.formBuilder.group({
            name: ['', [Validators.required, Validators.pattern(/^[a-zA-Z\u00C0-\u024F\u1E00-\u1EFF ]+$/)]],
            lastname: ['', [Validators.required, Validators.pattern(/^[a-zA-Z\u00C0-\u024F\u1E00-\u1EFF ]+$/)]],
            email: ['', [Validators.required, Validators.pattern(/\S+@\S+\.\S+/)]],
            password: ['', [Validators.required, Validators.minLength(8)]],
            confirm_password: ['', [Validators.required, Validators.minLength(8)]],
            code: ['']
        }, { validator: PasswordValidation.MatchPassword });

        this.activatedRoute.queryParamMap.subscribe((val: any) => {
            this.authService.getGuestEmail(val.params.param1).subscribe(res => {
                this.form.patchValue({
                    code: val.params.param1,
                    email: res.email
                });
            });
        });
    }

    register() { // No localStorage/sessionStorage used. Probably going to store email in sessionstorage
        this.snackbar.open(this.translateService.instant('REGISTER.LOADING'), 'INFO', {
            horizontalPosition: this.horizontalPosition,
            verticalPosition: this.verticalPosition,
            duration: 99999,
            panelClass: 'blue',
        });

        localStorage.removeItem('authToken');
        sessionStorage.removeItem('authToken');
        this.authService.registerGuest({
            firstname: this.form.get('name').value,
            lastname: this.form.get('lastname').value,
            code: this.form.get('code').value,
            password: this.form.get('password').value
        }).subscribe((res: any) => {

            this.authService.login({
                email: this.form.get('email').value,
                password: this.form.get('password').value 
            }).subscribe(_res => {

                sessionStorage.setItem('authToken', _res.token);
                sessionStorage.setItem('authId', _res.user);
                this.authService.isAuthenticated = true;
                this.authService.getProfile(_res.user).subscribe(user => {
                    
                    this._fuseConfigService.config = {
                        user: {
                            id: user.id,
                            firstname: user.firstname,
                            lastname: user.lastname,
                            email: user.email,
                            type: user.type,
                            status: user.status,
                            organization: {
                                id: user.organization_id,
                                name: user.organization_name,
                                description: user.organization_description,
                                domain: user.organization_domain,
                                image_url: user.organization_image_url
                            }
                        }
                    };
                    this._fuseNavigationService.setNavigationItem(user.type);
                    
                    this.badgesService.fetchBadges().subscribe(data_badges => {
                        sessionStorage.setItem('badges', JSON.stringify(data_badges));
                        this._fuseConfigService.config = {
                            badges: data_badges
                        }
                        this._fuseConfigService.config = {
                            layout: {
                                navbar: {
                                    hidden: false
                                }
                            }
                        }
                        this.snackbar.dismiss();
                        this.router.navigate(['home']);
                    }, err => {
                        this.snackbar.open(err.error.message, 'ERROR', {
                            horizontalPosition: this.horizontalPosition,
                            verticalPosition: this.verticalPosition,
                            duration: 5000,
                            panelClass: 'red'
                        });
                    });
                }, err => {
                    this.snackbar.open(err.error.message, 'ERROR', {
                        horizontalPosition: this.horizontalPosition,
                        verticalPosition: this.verticalPosition,
                        duration: 5000,
                        panelClass: 'red'
                    });
                });
            }, err => {
                this.snackbar.open(err.error.message, 'ERROR', {
                        horizontalPosition: this.horizontalPosition,
                        verticalPosition: this.verticalPosition,
                        duration: 5000,
                        panelClass: 'red'
                    });
            });
        }, err => {
            this.snackbar.open(err.error.message, 'ERROR', {
                horizontalPosition: this.horizontalPosition,
                verticalPosition: this.verticalPosition,
                duration: 5000,
                panelClass: 'red'
            });
        });
    }
}